import React, { Component, lazy, Suspense } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Container,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
  Pagination,
  PaginationItem,
  PaginationLink,
} from 'reactstrap';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { saveAs } from 'file-saver';
import Image from '../../assets/img/brand/loading2.gif'

const states = {
  false: "success",
  true: "warning",
  declined: "secondary",
};

class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      deliveries : [],payments:[],
      startDate: new Date(),
      endDate: new Date(),
      payTotal : 0,
      showAni : false,
    };
    this.updateAcceptance=this.updateAcceptance.bind(this);
  }

  componentDidMount(){
    this.getDeliveries();
    this.getPayments();
  }

  //fetch deliveries from database
  getDeliveries= async()=>{
    var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
    await axios.post('/updatedelitable',{
      startDate : stDate,
      endDate : enDate
    }).then(res =>{
      this.setState({
        deliveries:res.data
      })
    })
  }
  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  handleClick = () => {
    console.log('Click happened');
  }
//fetch payments from database
getPayments=async()=>{
  var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
  await axios.post('/getdailypayments',{
    startDate : stDate,
    endDate : enDate
  }).then(res=>{
    this.setState({
      payments : res.data
    })
  })
  var total=0
  for(var i=0; i<this.state.payments.length; i++){
    total+=this.state.payments[i].paymentsAmount;
  }
  this.setState({
    payTotal:total,
  })
}

//update delivery table in database
 updateAcceptance(rowIndex,row){
   if(!rowIndex.localeCompare('Pending..')){
     axios.post('/updateacceptance',{
       deliveryAcceptance :'Accepted',
       deliveryNoteNO : row.deliveryNoteNO,
     }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('SuccessfullyAdded')){
        this.getDeliveries();
      }
      else{
        this.getDeliveries();
        alert('Failed')
      }
    })
   }
   else{
     axios.post('/updateacceptance',{
       deliveryAcceptance : 'Pending..',
       deliveryNoteNO:row.deliveryNoteNO
     }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('SuccessfullyAdded')){
        this.getDeliveries();
      }
      else{
        alert('Failed')
      }
    })
   }
   
 }
 onCellBButtonDouClick=async(rowIndex,row)=>{
  if(!rowIndex.localeCompare('Accepted')){
  axios.post('/updateacceptance',{
    deliveryAcceptance : 'Rejected',
    deliveryNoteNO:row.deliveryNoteNO
  }).then(res =>{
   var status=res.data.status;
   if(!status.localeCompare('SuccessfullyAdded')){
     this.getDeliveries();
   }
   else{
     alert('Failed')
   }
 })
  }
 }
 //DeliveryTable cellbutton handler
  cellButton(rowIndex,row) {
    if(!rowIndex.localeCompare('Pending..')){
      return (
        <Button block color="warning" 
        onClick={() => 
          this.updateAcceptance(rowIndex,row)}
          onDoubleClick={()=>{this.onCellBButtonDouClick(rowIndex,row)}}>
          { rowIndex }
        </Button>
        )
    }
    else if(!rowIndex.localeCompare('Rejected')){
      return (
        <Button block color="danger" 
        onClick={() => 
          this.updateAcceptance(rowIndex,row)}
          onDoubleClick={()=>{this.onCellBButtonDouClick(rowIndex,row)}}>
          { rowIndex }
        </Button>
        )
    }
  	else{
      return (
        <Button block color="success" 
        data-letter={rowIndex,row}
        onClick={() => 
          this.updateAcceptance(rowIndex,row)}
          onDoubleClick={()=>{this.onCellBButtonDouClick(rowIndex,row)}}>
          { rowIndex }
        </Button>
        )
    }
  }
  //formate date in delivery table
  dateFormater(cell , row){
    var stDate = new Date(cell);
    var date = stDate.getFullYear() + '-' + (stDate.getMonth() + 1) + '-' + stDate.getDate();
     return `${date}`
   }
  // price formatter
  priceFormat(cell){
    return cell.toFixed(2);
  } 

     //Date change handlers
  handleStartDateChange = date =>{
    this.setState({
      startDate:date
    },()=>{
      this.getDeliveries();
      this.getPayments();
    })
  };
  handleEndDateChange = date =>{
    this.setState({
      endDate:date
    },()=>{
      this.getDeliveries();
      this.getPayments();
    })
  };

    //downloading delivery report pdf
    createAndDownloadDeliveryPdf = async () => {
      this.setState({
        showAni:true
      })
      var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
      var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
      await axios.post('/getDailyDeliveryReport', {
        deliveries:this.state.deliveries,
        startDate :stDate,
        endDate : enDate
      })
        .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
        .then((res) => {
          const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
  
          saveAs(pdfBlob, 'DailyDeliveries.pdf');
        })
        this.setState({
          showAni:false
        })
    }

    //Payments Report

    createAndDownloadPaymentPdf = async () => {
      this.setState({
        showAni:true
      })
      var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
      var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
      await axios.post('/getDailyPaymentReport', {
        payments:this.state.payments,
        startDate :stDate,
        endDate : enDate,
        payTotal :this.state.payTotal.toFixed(2)
      })
        .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
        .then((res) => {
          const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
  
          saveAs(pdfBlob, 'DailyPayments.pdf');
        })
        this.setState({
          showAni:false
        })
    }

    //Event handlers for delivery table

onAfterDeleteDelivery= async (rowKeys)=> {
  await axios.post('/deletedelivery',{
    deliveryNoteNO : rowKeys
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('Successfull')){
      alert('Successfully Deleted');
      this.getDeliveries();
    }
    else{
      alert('Failed To Delete')
    }
  })
}
onAfterDeletePayment= async (rowKeys)=> {
  await axios.post('/deletepayment',{
    payId : rowKeys
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('Successfull')){
      alert('Successfully Deleted');
      this.getPayments();
    }
    else{
      alert('Failed To Delete')
    }
  })
}

selectRowProp={
  mode:'radio',
  bgColor:'yellow'
}

options = {
  afterDeleteRow: this.onAfterDeleteDelivery // A hook for after droping rows.

};
secondOptions={
  afterDeleteRow: this.onAfterDeletePayment
}
  
  render() {

    return (
      <div>
        <Container>
          <Row>
            <Col xs="14" sm="12">
              <Card>
                <CardHeader>
                    <strong>Delivery Details</strong>
                    <FormGroup row>
                 
                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
                   <Grid container justify="space-around">
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="Start Date"
                       format="yyyy-MM-dd"
                       value={this.state.startDate}
                       onChange={this.handleStartDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     <Col xs="2" md="3">
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="End Date"
                       format="yyyy-MM-dd"
                       value={this.state.endDate}
                       onChange={this.handleEndDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     </Grid>
                   </MuiPickersUtilsProvider>
                   </FormGroup>
                </CardHeader>
                <CardBody style={{backgroundColor:"#d9d9d9"}}>
                <div>
                <BootstrapTable data={this.state.deliveries } pagination scrollTop={'Bottom'} search={ true } exportCSV={ true }
                bodyStyle={ { border: 'green 1px solid' }}
                containerStyle={ { border: '#FFBB73 2.5px solid' } }
                selectRow={this.selectRowProp} deleteRow={true} options={this.options}>
     
                <TableHeaderColumn width="80" tdStyle={ { whiteSpace: 'normal' } } dataField='deliveryNoteNO' 
                     isKey >Delivery <br/> Note No</TableHeaderColumn>
                <TableHeaderColumn width="100" tdStyle={ { whiteSpace: 'normal' } } dataField='vehicle_vehicleNumber'
                      >Lorry</TableHeaderColumn>
                <TableHeaderColumn width="100" tdStyle={ { whiteSpace: 'normal' } } dataField='deliveryDistance'
                      >Distance<br/>(km)</TableHeaderColumn>
                <TableHeaderColumn width="100" tdStyle={ { whiteSpace: 'normal' } } dataField="deliveryDate"
                      dataFormat={this.dateFormater}>Delivery<br/> Date</TableHeaderColumn>
                <TableHeaderColumn width="100" tdStyle={ { whiteSpace: 'normal' } } dataField="deliveryDepartureTime"
                      >Departure</TableHeaderColumn>
                <TableHeaderColumn width="130" tdStyle={ { whiteSpace: 'normal' } } dataField='uploadingPlaceAddress'
                      >Unloading<br/> Place</TableHeaderColumn>
                <TableHeaderColumn width="130" tdStyle={ { whiteSpace: 'normal' } } dataField='companyName'
                      >Comapany</TableHeaderColumn>
                <TableHeaderColumn width="100" tdStyle={ { whiteSpace: 'normal' } } dataField='driverName'
                      >Driver</TableHeaderColumn>
                <TableHeaderColumn width="200" tdStyle={ { whiteSpace: 'normal' } } dataField='deliveryAcceptance'
                     dataFormat={this.cellButton.bind(this)} >Status</TableHeaderColumn>

                </BootstrapTable>
 
            
                  </div>
                </CardBody>
                <CardFooter>
                  <FormGroup row>
                  <Col md="10">
                  <Label>Total Deliveries : {this.state.deliveries.length}</Label>
                  </Col>
                  {
                  this.state.showAni?
                <div ><img src={Image} alt="loading..." /></div> 
                  :null
                }
                  <Col >
                  <Button color="info" onClick={()=>{this.createAndDownloadDeliveryPdf()}}>Download</Button>
                  </Col>
                  </FormGroup>
                </CardFooter>
              </Card>
            </Col>
          </Row>
          <Row>
            <Col>
              <Card>
                <CardHeader>
                  <strong>Payments</strong>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
              <div>
               <BootstrapTable data={this.state.payments} pagination scrollTop={'Bottom'} search={true}
                  bodyStyle={ { border: 'green 1px solid' }}
                  containerStyle={ { border: '#FFBB73 2.5px solid' } } exportCSV={ true }
                  selectRow={this.selectRowProp} deleteRow={true} options={this.secondOptions}>
                  <TableHeaderColumn width='100' tdStyle={ { whiteSpace: 'normal' } } isKey dataField="paymentsId">Pay No</TableHeaderColumn>
                  <TableHeaderColumn width='100' tdStyle={ { whiteSpace: 'normal' } }  dataField="paymentsDate"
                  dataFormat={this.dateFormater.bind(this)}>Date</TableHeaderColumn>
                  <TableHeaderColumn width='150' tdStyle={ { whiteSpace: 'normal' } } dataField="vehicle_vehicleNumber">Vehicle</TableHeaderColumn>
                  <TableHeaderColumn width='150' tdStyle={ { whiteSpace: 'normal' } } dataFormat={this.priceFormat.bind(this)} dataField="paymentsAmount"
                  tdStyle={{'textAlign':'right'}}>Amount</TableHeaderColumn>
                  <TableHeaderColumn width='130' tdStyle={ { whiteSpace: 'normal' } } dataField="paymentTypeType">Type</TableHeaderColumn>
                  <TableHeaderColumn width='200' tdStyle={ { whiteSpace: 'normal' } } dataField="paymentsDescription">Description</TableHeaderColumn>
               </BootstrapTable>
            </div>
            </CardBody>
            <CardFooter>
            <FormGroup row>
                  <Col md="10">
                      <Label>Total Payments : Rs {this.state.payTotal.toFixed(2)}</Label>
                  </Col>
                  <Col >
                {
                  this.state.showAni?
                <div ><img src={Image} alt="loading..." /></div> 
                  :null
                }
                </Col>
                  <Col >
                  <Button color="info" onClick={()=>{this.createAndDownloadPaymentPdf()}}>Download</Button>
                  </Col>
            </FormGroup>
            </CardFooter>
            </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Dashboard;
